import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap';
import service from '../../_services/user-question.service';
import CopyButton from '../Dashboard/quiz/links/CopyButton';


function UserRow(props) {
  const quiz = props.quiz;

  const quizLink = `user-quiz/${quiz.quizLink}`;
  const responseLink = `/responses/${quiz.quizLink}`;
  const index = props.index;
  return (
    <tr key={index.toString()}>
      <td> {index}</td>
      <th scope="row"> <Link to={quizLink}>{quiz.quizLink}</Link></th>
      <td>   <CopyButton user={quiz.userId} text={quiz.quizLink} /></td>
      <th scope="row"> <Link to={responseLink}><Button>responses</Button></Link></th>
    </tr>
  )
}

class Users extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: []
    };
  }
  async getQuizzes() {
    await service.getLoggedInUserQuiz().then(res => {
      console.log(res);

      this.setState({
        data: res.data.data
      })
    }).catch(err => {
      console.log(err);

    })
  }
  async componentDidMount() {
    await this.getQuizzes();
  }
  render() {

    const quizList = this.state.data;

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={12}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Quizzes you created
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">id</th>
                      <th scope="col">quiz id</th>
                      <th scope="col"> share link</th>
                      <th scope="col"> View Responses</th>

                    </tr>
                  </thead>
                  <tbody>
                    {quizList.map((quiz, index) =>
                      <UserRow key={index} index={index+1} quiz={quiz} />
                    )}
                  </tbody>
                </Table>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Users;
